a{
  text-decoration: none;
  color: #333;
  font-size: 12px;
}
.loginform{
  text-align: center;
  width: 350px;
  margin: 0px auto;
  display: flex;
  flex-direction: column;
}
.loginform input{
  text-align: start;
  height: 50px;
  margin: 7px 0px;
  border: 1px solid #ccc;
}
.loginform input[type='submit']{
  text-align: center;
  font-weight: bold;
  background: #5f0080;
  margin: 5px 0px;
  border: none;
  cursor: pointer;
  color: #fff;
  font-size: 1rem;
}
.loginform input[type='submit']:last-child{
  border: 2px solid #5f0080;
  background: #fff;
  color: #333;
}
.loginfind{
  text-align: end;
  padding-bottom: 10px;
}
.loginfind a:first-child::after{
  content: '|';
  margin: 2px 5px;
}