*{
  margin: 0px;
  padding: 0px;
}
ul, li{
  list-style: none;
  position: relative;
}
.pd_li{
  width: 1200px;
  margin: 0px auto;
  display: flex;
  justify-content: space-around;
  gap: 20px;
}
.pd_li li{
  width: 25%;
  overflow: hidden;
}
.pd_li p{
  font-size: 12px;
  padding: 10px 0px;
}
.pd_li li p:first-child{
  text-align: center;
  transition: .5s;
  overflow: hidden;
  overflow: hidden;
}
.pd_li li p:first-child:hover{
  scale: 1.1;
}
.pd_li li P:nth-child(2){
  font-weight: bold;
  font-size: 1rem;
}
.pd_li li p:nth-child(3){
  font-weight: bold;
  font-size: 1rem;
  color: red;
}
.pd_li li p:last-child{
  color: #fff;
  font-size: 1.5rem;
  position: absolute;
  top: 65%; right: 10px;
  transform: translateY(-50%);
  padding: 20px;
  background: rgba(95, 0, 128, .5);
  border-radius: 100%;
  cursor: pointer;
}
.pd_li li img{
  width: 100%;
  object-fit: cover;
}
